        showModalDeclineInfo() {
            this.$modal.show(
                RadioModal,
                {
                    title: 'Decline',
                    headItem: 'Passport',
                    headText: '782987923',
                    content: 'Choose the reason to decline',
                    no: 'Cancel',
                    yes: 'Decline',
                },
                {
                    height: 'auto',
                    width: '572px',
                },
            )
        }

        showModalDecline() {
            this.$modal.show(
                RadioModal,
                {
                    title: 'Decline Video',
                    content: 'Choose the reason to declinee',
                    no: 'Cancel',
                    yes: 'Decline',
                },
                {
                    height: 'auto',
                    width: '572px',
                },
            )
        }

        showModalApprove() {
            this.$modal.show(
                SimpleModal,
                {
                    title: 'Approve Video',
                    content: 'All details are right. Want to approve?',
                    no: 'Cancel',
                    yes: 'Approve',
                    yesType: 'success',
                },
                {
                    height: 'auto',
                    width: '376px',
                },
            )
        }

        showTab(event) {
            const btn = event.target.closest('.tab_btn')
            const index = btn.getAttribute('data-index')
            this.tabs.forEach((item) => {
                // eslint-disable-next-line
                item.active = false
            })
            this.tabs[index].active = true
        }

        hideDaD() {
            this.visibleDaD = false
        }

        showDaD() {
            this.visibleDaD = true
        }

        showModalPhoto(event) {
            const src = event.target.getAttribute('src')
            this.$modal.show(
                PhotoModal,
                {
                    src,
                },
                {
                    width: '768px',
                    height: '529px',
                },
            )
        }
}
