


























































import {
    Component, Vue, Prop, Emit,
} from 'vue-property-decorator'
import UiButton from '../UiButton.vue'
import UiCheckbox from '../UiCheckbox.vue'

@Component({
    components: { UiButton, UiCheckbox },
})
export default class RadioModal extends Vue {
    @Prop() title: string

    @Prop() headItem: string

    @Prop() headText: string

    @Prop() content: string

    @Prop() no: string

    @Prop() yes: string

    disable: string = 'disable'

    hideModal() {
        this.$emit('close')
    }

    undisableBtn() {
        this.disable = ''
    }
}
