















































import { Component, Prop, Vue } from 'vue-property-decorator'
import UserFoto from './UserFoto.vue'

@Component({
    components: {
        UserFoto,
    },
})
export default class DropDownComent extends Vue {
    @Prop() list: any

    @Prop() placeholder: string

    @Prop() size: string

    @Prop() note: boolean

    topPosition: number = 0

    leftPosition: number = 0

    showContent() {
        // TODO: incorrect logic
        let btn
        let widthContent = 256
        if (this.size === 'big') {
            btn = this.$refs.buttonItemBig
        } else {
            btn = this.$refs.buttonItem
            widthContent = widthContent / 2 + 10
        }
        const heightBtn = btn.clientHeight
        const widthtBtn = btn.clientWidth
        this.topPosition = btn.getBoundingClientRect().top + heightBtn
        this.leftPosition = btn.getBoundingClientRect().left - widthContent + widthtBtn
    }
}
